import React from 'react';
import './index.scss';

class RulesPopup extends React.PureComponent {
  render() {
    if (!this.props.showPopup) {
      return null;
    }
    return (
      <div className="popup">
        <div className="popup_inner">
          <div className="header">
            <span className="title">Th l chng trnh</span>
            <button
              className="icclose_page_black"
              onClick={this.props.onClosePress}
            >
              <img
                className="image"
                src={require('./images/cancel@3x.png')}
                alt=""
              />
            </button>
          </div>
          <div className="content">
            <span className="normal">
              Mi ngi chi c 1 lt chi, mi lt chi gm 5 Vng, mi vng
              ko di 30 giy. Cc mn s xut hin ngu nhin v bin mt  cc
              v tr, chn ng mn bn c +1 im, chn nhm mn bn s b -1
              im.
              <br />
              <br />
              <b>- Vng 1:</b> Chn bnh Big Mac
              <br />
              <b>- Vng 2:</b> Chn Khoai Ty Chin
              <br />
              <b>- Vng 3:</b> Chn G Rn
              <br />
              <b>- Vng 4:</b> Chn Cm Trng Heo Nng
              <br />
              <b>- Vng 5:</b> Chn Happy Meal Kt thc
              <br /> <br />5 Vng tng ng vi s im bn c c s nhn
              c nhng phn qu tng ng. Hy nhanh tay kim tht nhiu im
               nhn c cc phn qu c gi tr nh. Chc bn chi game thnh
              cng.
            </span>
          </div>
        </div>
      </div>
    );
  }
}

class HomePage extends React.PureComponent {
  state = {
    showPopup: false
  };
  handleClosePress = () => {
    this.setState({
      showPopup: false
    });
  };
  handleOpenPopup = () => {
    this.setState({
      showPopup: true
    });
  };

  render() {
    return (
      <div className="home-page">
        <button
          className="button"
          onClick={() => {
            const { history } = this.props;
            history.push('./play');
          }}
        >
          <span className="title">CHI GAME</span>
        </button>

        <button className="rules" onClick={this.handleOpenPopup}>
          <span className="title">Th l chng trnh</span>
        </button>
        <img
          className="firewall"
          src={require('./images/firewall@3x.png')}
          alt=""
        />
        <RulesPopup
          showPopup={this.state.showPopup}
          onClosePress={this.handleClosePress}
        />
      </div>
    );
  }
}

export default HomePage;
